'use client';

import { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { mockSearchEverything } from '@/lib/mockNewsAPI';
import { Article } from '@/types/news';
import NewsCard from '@/components/NewsCard';
import SearchForm from '@/components/SearchForm';
import LoadingSkeleton from '@/components/LoadingSkeleton';

const SearchResults = ({ articles }: { articles: Article[] }) => {
  if (articles.length === 0) {
    return (
      <motion.div 
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="text-center py-12"
      >
        <div className="max-w-md mx-auto">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-16 w-16 mx-auto text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
          <h3 className="mt-4 text-lg font-medium text-gray-700">No results found</h3>
          <p className="mt-1 text-gray-500">Try adjusting your search or filters</p>
        </div>
      </motion.div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-8"
    >
      <AnimatePresence>
        {articles.map(article => (
          <motion.div
            key={article.url}
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 0.3 }}
          >
            <NewsCard article={article} />
          </motion.div>
        ))}
      </AnimatePresence>
    </motion.div>
  );
};

const SearchPage = () => {
  const [articles, setArticles] = useState<Article[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const availableSources = ['Tech News', 'Web Dev Weekly', 'React Blog'];

  const handleSearch = async (filters: { q: string }) => {
    setError(null);
    setIsLoading(true);
    
    try {
      const { articles } = await mockSearchEverything();
      setArticles(articles);
    } catch (err) {
      setError('Failed to fetch search results. Please try again later.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="container mx-auto py-8 px-4">
      <motion.h1 
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        className="text-3xl font-bold mb-8"
      >
        Search News
      </motion.h1>
      
      <SearchForm 
        onSearch={handleSearch} 
        availableSources={availableSources} 
      />
      
      <AnimatePresence>
        {error && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
            className="bg-red-50 border-l-4 border-red-500 p-4 mb-6 rounded"
          >
            <p className="text-red-700">{error}</p>
          </motion.div>
        )}
      </AnimatePresence>
      
      {isLoading ? (
        <LoadingSkeleton />
      ) : (
        <SearchResults articles={articles} />
      )}
    </main>
  );
};

export default SearchPage;