'use client';

import { useState } from 'react';
import { motion } from 'framer-motion';

interface SearchFilters {
  q: string;
  from?: string;
  to?: string;
  sources?: string[];
  sortBy?: 'relevancy' | 'popularity' | 'publishedAt';
}

interface SearchFormProps {
  onSearch: (filters: SearchFilters) => void;
  availableSources: string[];
}

export default function SearchForm({ onSearch, availableSources }: SearchFormProps) {
  const [query, setQuery] = useState('');
  const [filters, setFilters] = useState<Omit<SearchFilters, 'q'>>({});
  const [showFilters, setShowFilters] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSearch({ q: query, ...filters });
  };

  return (
    <motion.form 
      onSubmit={handleSubmit}
      className="bg-white p-6 rounded-xl shadow-lg"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
    >
      <div className="mb-4">
        <div className="relative">
          <input
            type="text"
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
            placeholder="Search news..."
            required
          />
          <button 
            type="button"
            onClick={() => setShowFilters(!showFilters)}
            className="absolute right-3 top-3 text-gray-400 hover:text-blue-500 transition-colors"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z" />
            </svg>
          </button>
        </div>
      </div>

      {showFilters && (
        <motion.div
          initial={{ opacity: 0, height: 0 }}
          animate={{ opacity: 1, height: 'auto' }}
          transition={{ duration: 0.2 }}
          className="overflow-hidden"
        >
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">From Date</label>
              <input
                type="date"
                onChange={(e) => setFilters({...filters, from: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">To Date</label>
              <input
                type="date"
                onChange={(e) => setFilters({...filters, to: e.target.value})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">Sort By</label>
              <select 
                onChange={(e) => setFilters({...filters, sortBy: e.target.value as any})}
                className="w-full px-3 py-2 border border-gray-300 rounded-md"
              >
                <option value="publishedAt">Newest First</option>
                <option value="relevancy">Relevance</option>
                <option value="popularity">Popularity</option>
              </select>
            </div>
          </div>
          {availableSources.length > 0 && (
            <div className="mb-4">
              <label className="block text-sm font-medium text-gray-700 mb-2">Sources</label>
              <div className="flex flex-wrap gap-2">
                {availableSources.map(source => (
                  <label key={source} className="inline-flex items-center">
                    <input
                      type="checkbox"
                      onChange={(e) => {
                        const sources = filters.sources || [];
                        if (e.target.checked) {
                          setFilters({...filters, sources: [...sources, source]});
                        } else {
                          setFilters({...filters, sources: sources.filter(s => s !== source)});
                        }
                      }}
                      className="rounded border-gray-300 text-blue-600 focus:ring-blue-500"
                    />
                    <span className="ml-2 text-sm text-gray-700">{source}</span>
                  </label>
                ))}
              </div>
            </div>
          )}
        </motion.div>
      )}

      <motion.button
        type="submit"
        whileHover={{ scale: 1.02 }}
        whileTap={{ scale: 0.98 }}
        className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg hover:bg-blue-700 transition-colors font-medium"
      >
        Search Articles
      </motion.button>
    </motion.form>
  );
}